<template>
  <div class="account__comments__wrapper">
    <div class="account__comments">
    <div>
      <Loader v-if="loading" />
      <div 
        class="account__comments__inner d-flex flex-row" 
        v-for="(comment, index) in commentFirst" :key="index">
          <div class="account__comments__left d-flex flex-row">
              <v-img 
                v-if="isImage && comment.authorImage"
                :src="require('../../../../server/' + comment.authorImage)" 
                class="account__comments__logo">
              </v-img>
              <div v-else class="account__comments__logo"></div>
              <div class="account__comments__text">
                <router-link 
                  class="account__comments__name" 
                  :to="{name: 'Reload', 
                  params: {id: comment.authorId}}">
                    {{ comment.authorName }} {{ comment.authorLastName }}
                </router-link>
                <p>{{ comment.text }}</p>
              </div>
          </div>
          <div class="account__comments__right">
            <v-icon 
              v-if="isUser || comment.authorId == dataDB.id"
              class="account__comments__icon" 
              @click="commentDelete(comment)"
            >cancel</v-icon>
            <p>{{ comment.date }}</p>
          </div>
      </div>
      <v-btn 
        @click="isOpen = true" 
        v-if="isOpen == false 
        && commentFirst.length > 3" 
        color="myPurple"
        class="account__comments__right__btn"
      >Показать еще</v-btn>
    </div>
    <div v-if="isOpen == true">
      <div 
        class="account__comments__inner d-flex flex-row" 
        v-for="(comment, index) in commentsArray.data" :key="index">
          <div class="account__comments__left d-flex flex-row">
              <router-link :to="{name: 'Reload', params: {id: comment.authorId}}">
                <v-img 
                  v-if="isImage && comment.authorImage"
                  :src="require('../../../../server/' + comment.authorImage)"
                  class="account__comments__logo"
                >
                </v-img>
                <div v-else class="account__comments__logo"></div>
              </router-link>
              <div class="account__comments__text">
                <router-link 
                  class="account__comments__name" 
                  :to="{name: 'Reload', 
                  params: {id: comment.authorId}}">
                    {{ comment.authorName }} {{ comment.authorLastName }}
                </router-link>
                <p>{{ comment.text }}</p>
              </div>
          </div>
          <div class="account__comments__right">
            <v-icon 
              v-if="isUser || comment.authorId == dataDB.id"
              class="account__comments__icon" 
              @click="commentDelete(comment)"
            >cancel</v-icon>
            <p>{{ comment.date }}</p>
          </div>
      </div>
    </div>
    </div>
    <div class="account__comments__input d-flex flex-row ">
      <v-img v-if="dataDB.additionally.img" :src="getImage" class="account__comments__logo">
      </v-img>
      <div v-else class="account__comments__logo"></div>
      <v-textarea 
        placeholder="Поделитесь вашим мнением по поводу поста."
        color="purple accent-4"
        solo
        rows="1"
        auto-grow
        v-model="comment.text"
      ></v-textarea>
      <v-btn 
        class="account__comments__send"
        v-if="comment.text.length < 4"
        color="myPurple"
        @click="minSymbols = true"
      >Отправить</v-btn>
      <v-btn 
        class="account__comments__send" 
        @click="commentAdd" 
        color="#7f5af0"
        v-else
      >Отправить</v-btn>
    </div>
    <v-snackbar v-model="commentLoad" :timeout="timer">
      <h3>Вы успешно поделились своим комментарием! Для просмотра обновите страницу</h3>
      <v-btn color="myPurple" style="color: #fff !important;" @click="commentLoad = false">Закрыть</v-btn>
    </v-snackbar>
    <v-snackbar v-model="isDelete" :timeout="timer2">
      <h3>Комментарий удален! Для просмотра обновите страницу</h3>
      <v-btn color="myPurple" style="color: #fff !important;" @click="isDelete = false">Закрыть</v-btn>
    </v-snackbar>
    <v-snackbar v-model="minSymbols" :timeout="timer">
      <h3>Комментарий должен содержать больше 4 символов</h3>
      <v-btn color="myPurple" style="color: #fff !important;" @click="commentLoad = false">Закрыть</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
import Loader from "../Loader.vue";
import { CommentsPost } from "../../models/models.js";
import "../../scss/comments.scss";

export default {
  name: "AccountComments",
  components: { Loader },
  data() {
    return {
      dataDB: "",
      isOpen: false,
      minSymbols: false,
      comment: {
        authorName: "",
        authorLastName: "",
        authorId: "",
        text: "",
        img: "",
        id: "",
        postId: "",
        date: "",
      },
      commentsList: [],
      commentLoad: false,
      commentFirst: [],
      commentsArray: [],
      timer: 5000,
      timer2: 2000,
      isDelete: false,
      loading: true,
      isImage: false
    }
  },
  props: {
    postId: Object,
    isUser: Boolean
  },
  async created() {
    this.dataDB = JSON.parse(localStorage.getItem("user"))
    let response = await axios.get('/api/comments/find_comments/' + this.postId._id)
    this.commentsList = response.data
    this.loading = false
    if (CommentsPost) {
      this.commentsArray = await new CommentsPost(this.commentsList)
      this.commentFirst = this.commentsArray.shortComments
      this.isImage = true
    }
  },
  computed: {
    getImage() {
      return require('../../../../server/' + this.dataDB.additionally.img)
    },
  },
  methods: {
    async commentAdd() {
      this.comment.authorName = this.dataDB.firstname
      this.comment.authorLastName = this.dataDB.lastname
      this.comment.authorId = this.dataDB.id
      this.comment.id = Math.floor(Math.random() * (999999999 - 9999) + 9999)
      this.comment.postId = this.postId._id
      this.comment.authorImage = this.dataDB.additionally.img
      this.comment.date = moment().format('LL');

      await axios.put("/api/comments/add_comment", this.comment)
      this.commentLoad = true
      this.comment.text = ""
    },
    async commentDelete(comment) {
      await axios.delete("/api/comments/del_comment/" + comment.id)
      this.isDelete = true
    }
  },
};
</script>